	module.exports = {"data":{"wordpressPost":{"title":"Many UK patients with gonorrhoea prescribed outdated antibiotics","content":"<p><a href=\"http://bmjopen.bmj.com/content/5/5/e007776.full\">Many UK patients with gonorrhoea are being prescribed antibiotics that are no longer recommended for treating the infection by their family doctor (GP)</a>, reveals research published in <strong><em><a href=\"http://bmjopen.bmj.com/\">BMJ Open</a>.</em></strong></p>\n<p>This failure to keep abreast of national clinical guidance is of concern, given the global threat of antibiotic resistance, say the researchers.</p>\n<p>They base their findings on an analysis of electronic health records entered anonymously into the Clinical Practice Research Datalink— a large database containing the health records of around 5.5 million patients registered with 680 general practices around the UK—as well as information from anonymous monitoring of sexually transmitted infections in England.</p>\n<p>They looked particularly at how doctors in general practice had treated the two most commonly diagnosed bacterial sexually transmitted infections in England, Chlamydia and gonorrhoea, between 2000 and 2011.</p>\n<p>GPs diagnosed an estimated 193,000 people with Chlamydia and nearly 17,000 with gonorrhoea during this period, accounting for between 9% and16% of all Chlamydia cases and between 6% and 9% of all gonorrhoea cases in England.</p>\n<p>The number of diagnoses GPs made for Chlamydia rose substantially from 22.8/100,000 of the population in 2000 to 29.3/100,000 of the population in 2011. And the proportion of patients treated for this infection rose from around six in every 10 (60%) to almost eight out of 10 (78%).</p>\n<p>Most (90%) were prescribed an antibiotic recommended in national clinical guidance. But this was not the case for gonorrhoea.</p>\n<p>The number of diagnoses fluctuated between 3.2 to 2.4/100,000 of the population, while the proportion treated ranged between a third (just under 33%) and just over half (54%).</p>\n<p>Despite being discontinued as a recommended treatment for the infection in 2005, ciprofloxacin continued to be prescribed. This antibiotic accounted for more than four out of 10 prescriptions (42%) in 2007, and one in five in 2011.</p>\n<p>The bacterium that causes gonorrhoea, <em>Neisseria gonorrhoeae</em>, is adept at developing resistance to the antibiotics used to treat it, and the evidence from other research shows that over a third of gonorrhoea infections treated at sexual health clinics were resistant to ciprofloxacin, for example, while up to one in five cases may be resistant to penicillin.</p>\n<p>The researchers conclude that GPs make an important contribution to the diagnosis and treatment of bacterial sexually transmitted infections, but while most patients with Chlamydia are treated appropriately, “significant numbers” of those infected with gonorrhoea are not.</p>\n<p>“Treatment of infections with reduced susceptibility or resistance to the prescribed therapy may inadvertently facilitate onward transmission and risks infection complications,” they write.</p>\n<p>“Antimicrobial resistance in gonorrhoea is a global problem and may become an issue for Chlamydia in future,” they warn. “Practitioners should be alert to the likelihood of revisions to national treatment guidelines and of treatment failure in their patients.”<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"9731a4d4-3540-5b7b-b83e-03e9462f7b91"}}

